@import "./App.css";


/* Title Card */
.gp-title-card{
    background: url('./imgs/dc_zoo_background.jpg')  rgba(0,0,0,.7) no-repeat;
    background-size: cover;
    background-position: center;
    background-blend-mode: color;
    padding-top: 5px;
}

/* Images Section */

#gameplay-images{
    display: flex;
   justify-content: space-between;
    margin-top: 20px;
    margin-right: 0px;
    margin-bottom: 20px;
    padding-bottom: 150px;
    border-bottom: 2px solid var(--light-gray);
    

    /*border-right: 200px solid var(--dark-gray);*/
    
   
    z-index: 1;
    position: relative;
    overflow: hidden;
}

.slide-image {
    margin-right: 30px;
    margin-top: 75px;
    
    
}

#navigation-arrows {
    
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
  }



/* How to Play Section */
.how-to-section{
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-bottom: 2px solid var(--light-gray);
}

.how-to-section-content {
    display: flex;
    justify-content: space-between;
    align-items: start; 
    padding: 20px 150px;
}

.how-to-section .how-to-text .how-to-head{
    color: var(--white);
    font-size: 90px;
}

.how-to-section .how-to-text .how-to-subhead{
    margin-left: 50px;
    color: var(--white);
    font-size: 50px;
}

.how-to-section .how-to-text .how-to-details{
    margin-left: 100px;
    color: var(--white);
    max-width: 65%;
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 300px; /* Adjust as per your design needs */
}

.how-to-section .how-to-img{
    width: 580px;
    height: 450px;
    margin-top: 140px;
}

/* Features Section */
.feat-section .feat-head{
    margin-top: 150px;
    margin-left: 150px;
    color: var(--white);
    font-size: 90px;
}

.feat-section b{
    margin-left: 150px;
    color: var(--white);
    font-size: 25px;
}

.feat-section ul {
    color: var(--white);
    font-size: 30px;
    list-style-type: disc; /* to ensure bullet points are displayed */
    display: flex;
    flex-direction: column; /* stack list items vertically */
    margin-left: 200px;
    margin-bottom: 200px;
    padding: 0; /* remove default padding */
}

.feat-section .soul-content{
    margin-left: 200px;
    color: var(--white);
    max-width: 65%;
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 300px; /* Adjust as per your design needs */
}

